.animateme {
  width: 100%;
  height: 100%;
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.bg-bubbles {
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
}

.bg-bubbles li {
  width: 40px;
  height: 40px;
  opacity: 0;
  background-color: #353535;
  list-style: none;
  transition-timing-function: linear;
  animation: square 25s infinite;
  display: block;
  position: absolute;
  bottom: 60px;
}

.bg-bubbles li:nth-child(1) {
  background: none;
  border: 4px solid #353535;
  border-radius: 50em;
  left: 3%;
}

.bg-bubbles li:nth-child(2) {
  width: 75px;
  height: 75px;
  animation-duration: 17s;
  animation-delay: 2s;
  left: 6%;
}

.bg-bubbles li:nth-child(3) {
  width: 175px;
  height: 175px;
  animation-delay: 4s;
  left: 15%;
}

.bg-bubbles li:nth-child(4) {
  width: 90px;
  height: 90px;
  animation-duration: 22s;
  left: 40%;
}

.bg-bubbles li:nth-child(5) {
  background: none;
  border: 4px solid #353535;
  left: 70%;
}

.bg-bubbles li:nth-child(6) {
  width: 90px;
  height: 90px;
  background: none;
  border: 4px solid #353535;
  border-radius: 50em;
  animation-delay: 3s;
  left: 90%;
}

.bg-bubbles li:nth-child(7) {
  width: 60px;
  height: 60px;
  background: none;
  border: 4px solid #353535;
  animation-delay: 7s;
  left: 32%;
}

.bg-bubbles li:nth-child(8) {
  width: 50px;
  height: 50px;
  animation-duration: 40s;
  animation-delay: 15s;
  left: 55%;
}

.bg-bubbles li:nth-child(9) {
  width: 140px;
  height: 140px;
  background: none;
  border: 4px solid #353535;
  border-radius: 50em;
  animation-duration: 40s;
  animation-delay: 2s;
  left: 30%;
}

.bg-bubbles li:nth-child(10) {
  width: 60px;
  height: 60px;
  animation-delay: 11s;
  left: 90%;
}

.bg-bubbles li:nth-child(11) {
  width: 40px;
  height: 40px;
  border-radius: 50em;
  animation-delay: 13s;
  left: 10%;
}

.bg-bubbles li:nth-child(12) {
  width: 175px;
  height: 175px;
  background: none;
  border: 4px solid #353535;
  animation-delay: 7s;
  left: 55%;
}

.bg-bubbles li:nth-child(13) {
  width: 100px;
  height: 100px;
  border-radius: 50em;
  animation-delay: 8s;
  left: 65%;
}

@-webkit-keyframes square {
  0% {
    opacity: 0;
    transform: translateY(0);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 1;
    transform: translateY(-700px)rotate(600deg);
  }
}

@keyframes square {
  0% {
    opacity: 0;
    transform: translateY(0);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: translateY(-700px)rotate(600deg);
  }
}

