button, button:after {
  cursor: pointer;
  color: #fff;
  letter-spacing: 3px;
  background: linear-gradient(45deg, rgba(0, 0, 0, 0) 5%, #ff013c 5%);
  border: 0;
  outline: rgba(0, 0, 0, 0);
  padding: 16px 20px;
  font-size: 18px;
  line-height: 1;
  position: relative;
  box-shadow: 6px 0 #00e6f6;
}

button:after {
  --slice-0: inset(50% 50% 50% 50%);
  --slice-1: inset(80% -6px 0 0);
  --slice-2: inset(50% -6px 30% 0);
  --slice-3: inset(10% -6px 85% 0);
  --slice-4: inset(40% -6px 43% 0);
  --slice-5: inset(80% -6px 5% 0);
  content: "BACK TO HOME";
  text-shadow: -3px -3px #f8f005, 3px 3px #00e6f6;
  clip-path: var(--slice-0);
  background: linear-gradient(45deg, rgba(0, 0, 0, 0) 3%, #00e6f6 3%, #00e6f6 5%, #ff013c 5%);
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

button:hover:after {
  animation: glitch 1s steps(2, end);
}

@keyframes glitch {
  0% {
    clip-path: var(--slice-1);
    transform: translate(-20px, -10px);
  }

  10% {
    clip-path: var(--slice-3);
    transform: translate(10px, 10px);
  }

  20% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 10px);
  }

  30% {
    clip-path: var(--slice-3);
    transform: translate(0, 5px);
  }

  40% {
    clip-path: var(--slice-2);
    transform: translate(-5px);
  }

  50% {
    clip-path: var(--slice-3);
    transform: translate(5px);
  }

  60% {
    clip-path: var(--slice-4);
    transform: translate(5px, 10px);
  }

  70% {
    clip-path: var(--slice-2);
    transform: translate(-10px, 10px);
  }

  80% {
    clip-path: var(--slice-5);
    transform: translate(20px, -10px);
  }

  90% {
    clip-path: var(--slice-1);
    transform: translate(-10px);
  }

  100% {
    clip-path: var(--slice-1);
    transform: translate(0);
  }
}

